#side_menu {
  z-index: 2;
  width: 250px;
  height: 100vh;
  background: #fff;
  position: sticky;
  top: 0;
  padding-top: 5em;
  padding-bottom: 40px;
  padding-left: 2em;
  padding-right: 2em;
  transition: all 0.5s ease;
  border-right: 1px solid #e6e6e6;

  .nav_list {
    @extend %link;
    width: inherit;

    ul li i {
      background-color: rgb(241, 241, 241);
      height: 30px;
      width: 30px;
      line-height: 30px;
      padding: auto;
      border-radius: 5px;
      text-align: center;
    }
  }

  .mobile_top_bar {
    @extend %center;
    position: absolute;
    right: 0px;
    top: 0;
    height: 50px;
    width: 50px;
    background-color: transparent;
    color: var(--writing);
    cursor: pointer;
    transition: all 0.3s ease;
  }
  &.close {
    margin-right: 50px;
    .mobile_top_bar {
      right: -50px;
    }
    margin-left: -250px;
  }
}

@media screen and (max-width: 950px) {
  #side_menu {
    &.close {
      margin-right: 0;
    }
  }
}
