@mixin input($height: 50px, $width: 100%) {
  height: $height;
  width: $width;
  border: none;
  border-radius: 0;
  outline: none;
  padding-left: 14px;
  border: 1px solid gray;
  color: var(--ecriture);

  &:focus {
    border-color: var(--primary);
  }
}

@mixin clamp($limit: 3) {
  display: -webkit-box;
  -webkit-line-clamp: $limit;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
