#aside_bar {
  min-width: 300px;
  padding: 80px 32px 32px 40px;
  position: sticky;
  top: -1px;
  height: fit-content;
  min-height: 100vh;
  transition: all 0.5s ease;

  .mobile_top_aside {
    display: none;
    @extend %center;
    transition: all 0.3s ease;
  }
  form {
    .search_group {
      @extend %search_group;
    }
  }
}

@media screen and (max-width: 950px) {
  #aside_bar {
    position: fixed;
    top: -1px;
    right: 0;
    border-left: 1px solid #a3a3a3;
    .mobile_top_aside {
      border-left: 1px solid #a3a3a3;
      background-color: var(--background);
      display: flex;
      position: absolute;
      left: -1px;
      top: -1px;
      height: 50px;
      width: 50px;
      color: var(--writing);
      cursor: pointer;
      font-size: 22px;

      &::after {
        content: "";
        pointer-events: none;
        z-index: -1;
        position: absolute;
        top: -1px;
        right: 48px;
        height: inherit;
        width: 100vw;
        background-color: var(--backgroundWhite);
      }
    }
    &.close {
      .mobile_top_aside {
        border-left: 1px solid transparent;
        left: -50px;
        background-color: var(--backgroundWhite);
      }
      margin-right: -300px;
    }
  }
}
