@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
$font: "Poppins", Arial, sans-serif;

$background-blue-sky: #edf2f9;
$primary: #9c9ea1 !default;
$success: #198754 !default;
$secondary: #676767 !default;
$danger: #dc3545 !default;
$warning: #fd7e14 !default;
$auther: #151845 !default;

:root {
  --primary: #2196f3;
  --secondary: #676767;

  --red: #c7192e;
  --blue: #247ba0;
  --yellow: #ffc700;
  --green: #22a77d;

  --success: #2ecc71;
  --warning: #ffdd70;
  --danger: #c7192e;

  --blue-70: #247ba070;
  --yellow-70: #ffc70070;
  --orange-70: #ff990070;
  --green-70: #22a77d70;

  --color1: #50514f;

  --background: #f8f9fa;
  --backgroundWhite: #ffffff;
  --writing: #131523;
  --dark: #131523;

  --animate-duration: 500ms;
  --animate-delay: 0.9s;
}
